import React, { Fragment } from "react";
import { FaSchool } from "react-icons/fa";
import { VscOpenPreview } from "react-icons/vsc";

interface StepsHeaderProps {
  currentStep: number;
  stepState: {
    ONE: boolean;
    TWO: boolean;
  };
  setFormSteps: (value: { ONE: boolean; TWO: boolean }) => void;
}

const StepsHeader: React.FC<StepsHeaderProps> = (props) => {
  return (
    <Fragment>
      <div className="p-5">
        <div className="mx-4 p-4">
          <div className="flex items-center">
            {/* STEP ONE ================================== */}
            <div
              className={`flex items-center ${
                props.currentStep === 1
                  ? "text-white"
                  : props.stepState.ONE
                  ? "text-blue-500 cursor-pointer"
                  : "text-gray-500"
              } relative`}
              onClick={() =>
                props.stepState.ONE &&
                props.setFormSteps({
                  ONE: true,
                  TWO: false,
                })
              }
            >
              <div
                className={`rounded-full transition duration-500 ease-in-out h-12 w-12 py-3 border-2 ${
                  props.currentStep === 1
                    ? "border-blue-500 bg-blue-500"
                    : props.stepState.ONE
                    ? "border-blue-500 cursor-pointer"
                    : "border-gray-500"
                }`}
              >
                <FaSchool className="feather feather-bookmark w-full h-full" />
              </div>
              <div
                className={`absolute top-0 -ml-10 text-center mt-16 w-32 text-xs font-semibold uppercase ${
                  props.currentStep === 1
                    ? "text-blue-500"
                    : props.stepState.ONE
                    ? "text-blue-500 cursor-pointer"
                    : "text-gray-500"
                }`}
              >
                Position
              </div>
            </div>
            <div
              className={`flex-auto border-t-2 transition duration-500 ease-in-out ${
                props.stepState.ONE ? "border-blue-500" : "border-gray-300"
              }`}
            ></div>

            {/* STEP TWO ================================== */}
            <div
              className={`flex items-center ${
                props.currentStep === 4
                  ? "text-white"
                  : props.stepState.TWO
                  ? "text-blue-500 cursor-pointer"
                  : "text-gray-500"
              } relative`}
              onClick={() =>
                props.stepState.TWO &&
                props.setFormSteps({
                  ONE: false,
                  TWO: false,
                })
              }
            >
              <div
                className={`rounded-full transition duration-500 ease-in-out h-12 w-12 py-3 border-2 ${
                  props.currentStep === 4
                    ? "border-blue-500 bg-blue-500"
                    : props.stepState.TWO
                    ? "border-blue-500 cursor-pointer"
                    : "border-gray-500"
                }`}
              >
                <VscOpenPreview className="feather feather-database w-full h-full" />
              </div>
              <div
                className={`absolute top-0 -ml-10 text-center mt-16 w-32 text-xs font-semibold uppercase ${
                  props.currentStep === 4
                    ? "text-blue-500"
                    : props.stepState.TWO
                    ? "text-blue-500"
                    : "text-gray-500"
                }`}
              >
                Description
              </div>
            </div>
          </div>
        </div>
      </div>
    </Fragment>
  );
};

export default StepsHeader;
