import React, { Component } from "react";
import { BiReply } from "react-icons/bi";
import Modal, { ModalSize, Themes } from "../../components/Modal/Modal";
import { PositionAPI } from "../../interfaces";
import { placedCandidateInterface } from "../../interfaces/marks_publication";
import { capitalizeFirstLetter, getPositionById } from "../../shared/dataTest";
import {
  PostRequestStatusInterface,
  PostSchoolInterface,
} from "../../shared/interfaces";
import { getPlacementByRequest } from "./RequestTeacher";

interface ViewRequestProps {
  thisPost: PostSchoolInterface;
  setThisPost: (value: PostSchoolInterface | null) => void;
  setViewPost: (value: boolean) => void;
  systemPositions: PositionAPI[];
  schoolPlacements: placedCandidateInterface[];
  setPostId: (value: string) => void;
  setModalState: (value: "CREATE" | "UPDATE" | "RECEIVE" | null) => void;
  setPositionId: (value: string) => void;
  setRequestedPosts: (value: number) => void;
  setRequestExplanation: (value: string) => void;
  setFemaleOnPosition: (value: number) => void;
  setMaleOnPosition: (value: number) => void;
  setFormSteps: (value: { ONE: boolean; TWO: boolean }) => void;
  setStepState: (value: { ONE: boolean; TWO: boolean }) => void;
}

export class ViewRequest extends Component<ViewRequestProps> {
  render() {
    return (
      <Modal
        backDrop={true}
        theme={Themes.default}
        close={(): void => {
          this.props.setThisPost(null);
          this.props.setViewPost(false);
        }}
        displayClose={true}
        backDropClose={false}
        title={
          <div className="flex flex-row gap-4 items-center">
            <div className="flex flex-col">
              <span className="font-bold text-2xl text-gray-500">
                Viewing Post Request
              </span>
            </div>
          </div>
        }
        widthSizeClass={ModalSize.large}
        padding={{ title: true, body: false, footer: false }}
      >
        <section className="px-4 mx-auto mb-4 mt-3 flex items-start">
          <section className="w-full">
            <div className="flex flex-row item-center justify-left gap-3 mb-2">
              <span className="text-gray-500 italic">Position: </span>
              <span className="font-bold">
                {getPositionById(
                  this.props.systemPositions,
                  this.props.thisPost.position_id
                )
                  ? getPositionById(
                      this.props.systemPositions,
                      this.props.thisPost.position_id
                    )!.position_title
                  : "Unknown position"}
              </span>
            </div>
            <div className="flex flex-row item-center justify-left gap-3 mb-2">
              <span className="text-gray-500 italic">Requested posts: </span>
              <span className="font-bold text-xl">
                {this.props.thisPost.needed_posts}
              </span>
            </div>
            <div className="flex flex-row item-center justify-left gap-3 mb-2">
              <span className="text-gray-500 italic">Approved posts: </span>
              <span className="font-bold text-xl">
                {this.props.thisPost.requested_post_status !==
                PostRequestStatusInterface.INITIATED
                  ? this.props.thisPost.confirmed_posts
                  : "-"}
              </span>
            </div>
            <div className="flex flex-row item-center justify-left gap-3 mb-2">
              <span className="text-gray-500 italic">Placed posts: </span>
              <span className="font-bold text-xl">
                {this.props.thisPost.requested_post_status !==
                PostRequestStatusInterface.INITIATED ? (
                  <div
                    className="flex items-center gap-1 cursor-pointer"
                    onClick={() => this.props.setModalState("RECEIVE")}
                  >
                    {
                      getPlacementByRequest(
                        this.props.schoolPlacements,
                        this.props.thisPost._id
                      ).length
                    }
                    {getPlacementByRequest(
                      this.props.schoolPlacements,
                      this.props.thisPost._id
                    ).length > 0 && (
                      <BiReply className="bg-yellow-600 w-5 h-5 text-white" />
                    )}
                  </div>
                ) : (
                  "-"
                )}
              </span>
            </div>
            <div className="flex flex-row item-center justify-left gap-3 mb-2">
              <span className="text-gray-500 italic">Received posts: </span>
              <span className="font-bold text-xl">
                {this.props.thisPost.requested_post_status ===
                PostRequestStatusInterface.RECEIVED
                  ? this.props.thisPost.received_posts
                  : "-"}
              </span>
            </div>
            <div className="flex flex-row item-center justify-left gap-3 mb-2">
              <span className="text-gray-500 italic">Number of female: </span>
              <span className="font-bold text-xl">
                {this.props.thisPost.female_number}
              </span>
            </div>
            <div className="flex flex-row item-center justify-left gap-3 mb-2">
              <span className="text-gray-500 italic">Number of male: </span>
              <span className="font-bold text-xl">
                {this.props.thisPost.male_number}
              </span>
            </div>
            <div className="flex flex-row item-center justify-left gap-3 mb-2">
              <span className="text-gray-500 italic">Request reason: </span>
              <span className="font-bold text-xl">
                {this.props.thisPost.description}
              </span>
            </div>
            <div className="flex flex-row item-center justify-left gap-3 mb-2">
              <span className="text-gray-500 italic">Status: </span>
              <span
                className={`px-2 py-1 mx-1 font-semibold rounded ${
                  this.props.thisPost.requested_post_status ===
                  PostRequestStatusInterface.INITIATED
                    ? "text-gray-700 bg-gray-100 dark:bg-gray-700 dark:text-gray-100"
                    : this.props.thisPost.requested_post_status ===
                      PostRequestStatusInterface.CONFIRMED
                    ? "text-blue-700 bg-blue-100 dark:bg-blue-700 dark:text-blue-100"
                    : "text-green-800 bg-green-100 dark:bg-green-800 dark:text-green-100"
                }`}
              >
                {this.props.thisPost.requested_post_status ===
                PostRequestStatusInterface.INITIATED
                  ? "Pending"
                  : capitalizeFirstLetter(
                      this.props.thisPost.requested_post_status
                    )}
              </span>
            </div>
          </section>
          <div className="mb-3 flex items-center justify-end gap-2">
            {this.props.thisPost.requested_post_status ===
              PostRequestStatusInterface.INITIATED && (
              <button
                className="font-bold py-1 px-4 rounded bg-gray-500 text-white hover:bg-gray-700"
                onClick={() => {
                  this.props.setModalState("UPDATE");
                  this.props.setPostId(this.props.thisPost._id);
                  this.props.setPositionId(this.props.thisPost.position_id);
                  this.props.setRequestedPosts(
                    this.props.thisPost.needed_posts
                  );
                  this.props.setRequestExplanation(
                    this.props.thisPost.description
                  );
                  this.props.setFemaleOnPosition(
                    this.props.thisPost.female_number
                  );
                  this.props.setMaleOnPosition(this.props.thisPost.male_number);
                  this.props.setFormSteps({ ONE: true, TWO: false });
                  this.props.setStepState({ ONE: false, TWO: false });
                }}
                disabled={
                  this.props.thisPost.requested_post_status !==
                  PostRequestStatusInterface.INITIATED
                }
              >
                Edit
              </button>
            )}
            {this.props.thisPost.requested_post_status !==
              PostRequestStatusInterface.INITIATED &&
              getPlacementByRequest(
                this.props.schoolPlacements,
                this.props.thisPost._id
              ).length > 0 && (
                <button
                  className="font-bold py-1 px-2 rounded bg-gray-500 text-white hover:bg-gray-700"
                  onClick={() => this.props.setModalState("RECEIVE")}
                >
                  Receive
                </button>
              )}
          </div>
        </section>
      </Modal>
    );
  }
}

export default ViewRequest;
