import React, { Component } from "react";
import { BiReply } from "react-icons/bi";
import NoResult, { NoResultThemes } from "../../components/NoResult/NoResult";
import { PositionAPI } from "../../interfaces";
import {
  placedCandidateInterface,
  PlacedCandidateStatus,
} from "../../interfaces/marks_publication";
import { getPositionById } from "../../shared/dataTest";
import {
  PostRequestStatusInterface,
  PostSchoolInterface,
} from "../../shared/interfaces";
import { showRequestStatus } from "./ReceivePost";
import { getPlacementByRequest } from "./RequestTeacher";
import ViewRequest from "./ViewRequest";

interface RequestListProps {
  schoolPosts: PostSchoolInterface[];
  systemPositions: PositionAPI[] | null;
  setModalState: (value: "CREATE" | "UPDATE" | "RECEIVE" | null) => void;
  loadingRequest: boolean;
  filterByPosition: string;
  setFilterByPosition: (value: string) => void;
  setThisPost: (value: PostSchoolInterface | null) => void;
  setViewPost: (value: boolean) => void;

  schoolPlacements: placedCandidateInterface[];

  thisPost: PostSchoolInterface | null;
  setPostId: (value: string) => void;
  setPositionId: (value: string) => void;
  setRequestedPosts: (value: number) => void;
  setRequestExplanation: (value: string) => void;
  setFemaleOnPosition: (value: number) => void;
  setMaleOnPosition: (value: number) => void;
  setFormSteps: (value: { ONE: boolean; TWO: boolean }) => void;
  setStepState: (value: { ONE: boolean; TWO: boolean }) => void;
  viewPost: boolean;
  modalState: "CREATE" | "UPDATE" | "RECEIVE" | null;
}

interface RequestListState {
  searchInput: string;
}

class RequestList extends Component<RequestListProps, RequestListState> {
  constructor(props: RequestListProps) {
    super(props);

    this.state = {
      searchInput: "",
    };
  }

  setSearchInput = (value: string) => {
    this.setState({ searchInput: value });
  };

  render() {
    return (
      <div>
        <div className="my-2 flex sm:flex-row flex-col justify-between">
          <div className="flex flex-col md:flex-row">
            <select
              className="border-2 border-gray-700 bg-white p-2 rounded focus:outline-none"
              onChange={(e) => this.props.setFilterByPosition(e.target.value)}
              value={this.props.filterByPosition}
            >
              <option value="all">All Position</option>
              {this.props.systemPositions &&
              this.props.systemPositions.length > 0 ? (
                this.props.systemPositions.map((position, index) => (
                  <option key={index + 1} value={position.position_id}>
                    {position.position_title}
                  </option>
                ))
              ) : (
                <option value="">No Position found</option>
              )}
            </select>
          </div>
          <div className="block relative">
            <div className="pt-6 md:pt-0 md:pl-6">
              <button
                className="btn bg-primary text-white hover:bg-blue-600"
                onClick={() => this.props.setModalState("CREATE")}
                disabled={this.props.loadingRequest}
              >
                Request Teacher
              </button>
            </div>
          </div>
        </div>

        <div className="w-full py-2">
          <input
            onChange={(e) => this.setSearchInput(e.target.value)}
            type="text"
            value={this.state.searchInput}
            placeholder="Search..."
            className="w-full bg-gray-50 border-2 border-gray-400 py-2 px-3 rounded focus:border-gray-800"
            disabled={
              this.props.schoolPosts.length <= 0 || this.props.loadingRequest
            }
          />
        </div>

        <table className="w-full shadow" id="report_content">
          <thead className="bg-blue-50 border-b-2 border-gray-300 text-gray-600 font-bold text-sm">
            <tr>
              <td className="px-2 py-2">#</td>
              <td className="px-2 py-2">Position</td>
              <td className="px-2 py-2">Current F</td>
              <td className="px-2 py-2">Current M</td>
              <td className="px-2 py-2">Request</td>
              <td className="px-2 py-2">Approved</td>
              <td className="px-2 py-2">Placed</td>
              <td className="px-2 py-2">Received</td>
              <td className="px-2 py-2">Not Received</td>
              <td className="px-2 py-2">Vacant</td>
              <td className="px-2 py-2">Status</td>
              <td className="px-2 py-2"></td>
            </tr>
          </thead>
          <tbody>
            {this.props.schoolPosts.length > 0 ? (
              this.props.schoolPosts.map((schoolPost, index) => (
                <tr
                  key={index + schoolPost._id}
                  className="border-b border-gray-200 hover:bg-gray-100"
                >
                  <td className="px-2 py-3 text-left font-medium">
                    {index + 1}
                  </td>
                  <td className="py-3 px-1 text-left font-medium">
                    {this.props.systemPositions &&
                    getPositionById(
                      this.props.systemPositions,
                      schoolPost.position_id
                    )
                      ? getPositionById(
                          this.props.systemPositions,
                          schoolPost.position_id
                        )!.position_title
                      : "Unknown position"}
                  </td>
                  <td className="py-3 px-3 text-left text-base font-medium">
                    {schoolPost.female_number}
                  </td>
                  <td className="py-3 px-3 text-left text-base font-medium">
                    {schoolPost.male_number}
                  </td>
                  <td className="py-3 px-3 text-left text-base font-medium">
                    {schoolPost.needed_posts}
                  </td>
                  <td className="py-3 px-3 text-left text-base font-medium">
                    {schoolPost.requested_post_status !==
                    PostRequestStatusInterface.INITIATED
                      ? schoolPost.confirmed_posts
                      : "-"}
                  </td>
                  <td className="py-3 px-3 text-left text-base font-medium">
                    <div className="flex items-center gap-1">
                      {schoolPost.confirmed_posts &&
                        getPlacementByRequest(
                          this.props.schoolPlacements,
                          schoolPost._id
                        ).length}
                      {getPlacementByRequest(
                        this.props.schoolPlacements,
                        schoolPost._id
                      ).length > 0 && (
                        <BiReply
                          className="bg-yellow-600 w-5 h-5 text-white cursor-pointer"
                          onClick={() => {
                            this.props.setModalState("RECEIVE");
                            this.props.setThisPost(schoolPost);
                          }}
                        />
                      )}
                    </div>
                  </td>
                  <td className="py-3 px-3 text-left text-base font-medium">
                    {schoolPost.received_posts}
                  </td>
                  <td className="py-3 px-3 text-left text-base font-medium">
                    {schoolPost.confirmed_posts &&
                      getPlacementByRequest(
                        this.props.schoolPlacements,
                        schoolPost._id,
                        PlacedCandidateStatus.REJECTED
                      ).length}
                  </td>
                  <td className="py-3 px-3 text-left text-base font-medium">
                    {schoolPost.confirmed_posts &&
                      getPlacementByRequest(
                        this.props.schoolPlacements,
                        schoolPost._id,
                        "all"
                      ).length -
                        getPlacementByRequest(
                          this.props.schoolPlacements,
                          schoolPost._id,
                          PlacedCandidateStatus.PLACED
                        ).length}
                  </td>
                  <td>
                    <span
                      className={`px-2 py-1 font-semibold rounded ${
                        schoolPost.requested_post_status ===
                        PostRequestStatusInterface.INITIATED
                          ? "text-gray-700 bg-gray-100 dark:bg-gray-700 dark:text-gray-100"
                          : schoolPost.requested_post_status ===
                            PostRequestStatusInterface.CONFIRMED
                          ? "text-blue-700 bg-blue-100 dark:bg-blue-700 dark:text-blue-100"
                          : "text-green-800 bg-green-100 dark:bg-green-800 dark:text-green-100"
                      }`}
                    >
                      {showRequestStatus(
                        schoolPost.requested_post_status,
                        getPlacementByRequest(
                          this.props.schoolPlacements,
                          schoolPost._id,
                          "all"
                        ).length,
                        getPlacementByRequest(
                          this.props.schoolPlacements,
                          schoolPost._id,
                          PlacedCandidateStatus.PLACED
                        ).length,
                        getPlacementByRequest(
                          this.props.schoolPlacements,
                          schoolPost._id,
                          PlacedCandidateStatus.REJECTED
                        ).length
                      )}
                    </span>
                  </td>
                  <td className="py- text-left">
                    <div className="flex item-center justify-end gap-1">
                      <button
                        className="btn btn-blue transition ease-out duration-500 flex items-center"
                        title="View User Profile"
                        onClick={() => {
                          this.props.setThisPost(schoolPost);
                          this.props.setViewPost(true);
                        }}
                      >
                        Details
                      </button>
                      {schoolPost.requested_post_status ===
                        PostRequestStatusInterface.INITIATED && (
                        <button
                          className="btn bg-gray-500 text-white hover:bg-gray-700"
                          onClick={() => {
                            this.props.setModalState("UPDATE");
                            this.props.setPostId(schoolPost._id);
                            this.props.setPositionId(schoolPost.position_id);
                            this.props.setRequestedPosts(
                              schoolPost.needed_posts
                            );
                            this.props.setRequestExplanation(
                              schoolPost.description
                            );
                            this.props.setFemaleOnPosition(
                              schoolPost.female_number
                            );
                            this.props.setMaleOnPosition(
                              schoolPost.male_number
                            );
                            this.props.setFormSteps({ ONE: true, TWO: false });
                            this.props.setStepState({ ONE: false, TWO: false });
                          }}
                          disabled={this.props.loadingRequest}
                        >
                          Edit
                        </button>
                      )}
                      {getPlacementByRequest(
                        this.props.schoolPlacements,
                        schoolPost._id
                      ).length > 0 && (
                        <button
                          className="btn bg-green-600 text-white hover:bg-green-700"
                          onClick={() => {
                            this.props.setModalState("RECEIVE");
                            this.props.setThisPost(schoolPost);
                          }}
                          disabled={this.props.loadingRequest}
                        >
                          Receive
                        </button>
                      )}
                    </div>
                  </td>
                </tr>
              ))
            ) : (
              <tr>
                <td colSpan={12}>
                  <NoResult
                    theme={NoResultThemes.Dark}
                    value={"No request have been made!"}
                  />
                </td>
              </tr>
            )}
          </tbody>
        </table>

        {this.props.viewPost &&
          this.props.modalState === null &&
          this.props.thisPost &&
          this.props.systemPositions && (
            <ViewRequest
              thisPost={this.props.thisPost}
              setThisPost={this.props.setThisPost}
              setViewPost={this.props.setViewPost}
              systemPositions={this.props.systemPositions}
              schoolPlacements={this.props.schoolPlacements}
              setPostId={this.props.setPostId}
              setModalState={this.props.setModalState}
              setPositionId={this.props.setPositionId}
              setRequestedPosts={this.props.setRequestedPosts}
              setRequestExplanation={this.props.setRequestExplanation}
              setFemaleOnPosition={this.props.setFemaleOnPosition}
              setMaleOnPosition={this.props.setMaleOnPosition}
              setFormSteps={this.props.setFormSteps}
              setStepState={this.props.setStepState}
            />
          )}
      </div>
    );
  }
}

export default RequestList;
