import axios from "axios";
import React, { Component } from "react";
import {
  MdArrowBack,
  MdErrorOutline,
  MdMail,
  MdSmartphone,
  MdTaskAlt,
} from "react-icons/md";
import { connect } from "react-redux";
import { Auth, PushPopupAlert } from "../../actions";
import { fetchSchoolPosts, getSchoolPlacement } from "../../actions/schools";
import ActionConfirm, {
  ActionConfirmEnum,
} from "../../components/ActionConfirm/ActionConfirm";
import { PopupAlertData } from "../../components/Alert/Popup";
import { PositionAPI } from "../../interfaces";
import {
  placedCandidateInterface,
  PlacedCandidateStatus,
} from "../../interfaces/marks_publication";
import { PopupAlertTheme } from "../../interfaces/popupAlert";
import { StoreState } from "../../reducers";
import { getPositionById, isEmptyOrSpaces } from "../../shared/dataTest";
import {
  PostSchoolInterface,
  PostRequestStatusInterface,
} from "../../shared/interfaces";
import { API } from "../../utils/api";
import { errorToText } from "../../utils/errors";
import ViewPosition from "../Positions/ViewPosition";
import { getPlacementByRequest } from "./RequestTeacher";

export enum postShowStatus {
  PENDING = "Pending",
  CONFIRMED = "Confirmed",
  ALL_RECEIVED = "All received",
  PARTIAL_RECEIVED = "Received & Pending",
  ALL_REJECTED = "All rejected",
  PARTIAL_REJECTED = "Rejected & Pending",
  PARTIAL_RECEIVED_REJECTED = "Received & Rejected & Pending",
  RECEIVED_REJECTED = "Received & Rejected",
}
export const showRequestStatus = (
  status: PostRequestStatusInterface,
  placed: number,
  received: number,
  rejected: number
): postShowStatus => {
  if (status === PostRequestStatusInterface.RECEIVED) {
    if (placed === received) {
      return postShowStatus.ALL_RECEIVED;
    }
    if (placed === rejected) {
      return postShowStatus.ALL_REJECTED;
    }
    if (placed === rejected + received) {
      return postShowStatus.RECEIVED_REJECTED;
    }
    if (placed > rejected + received && received > 0 && rejected > 0) {
      return postShowStatus.PARTIAL_RECEIVED_REJECTED;
    }
    if (rejected === 0 && placed > received) {
      return postShowStatus.PARTIAL_RECEIVED;
    }
    if (received === 0 && placed > rejected) {
      return postShowStatus.PARTIAL_REJECTED;
    }
  }

  if (status === PostRequestStatusInterface.CONFIRMED) {
    return postShowStatus.CONFIRMED;
  }
  return postShowStatus.PENDING;
};

type ReceivePostProps = {
  viewPost: boolean;
  placementList: placedCandidateInterface[];
  thisPost: PostSchoolInterface;
  setThisPost: (value: PostSchoolInterface | null) => void;
  setModalState: (value: "CREATE" | "UPDATE" | "RECEIVE" | null) => void;
  formError: {
    element: string;
    msg: string;
  } | null;
  setFormError: (
    value: {
      element: string;
      msg: string;
    } | null
  ) => void;
  receivedPosts: number;
  setReceivedPosts: (value: number) => void;
  loadingRequest: boolean;
  setLoadingRequest: (value: boolean) => void;
  loadingPost: boolean;
  setLoadingPost: (value: boolean) => void;
  positionId: string;
  systemPositions: PositionAPI[];

  // REDUX ==========
  auth: Auth;
  fetchSchoolPosts: Function;
  getSchoolPlacement: Function;
  PushPopupAlert: (alert: PopupAlertData) => void;
};

type ReceivePostState = {
  placement_id: string;
  action: "RECEIVED" | "NOT_RECEIVED" | null;
  takeAction: boolean;
  comment: string;
  thisPlacement: placedCandidateInterface | null;
};

class ReceivePost extends Component<ReceivePostProps, ReceivePostState> {
  constructor(props: ReceivePostProps) {
    super(props);

    this.state = {
      placement_id: "",
      action: null,
      thisPlacement: null,
      takeAction: false,
      comment: "",
    };
  }
  setThisPlacement = (value: placedCandidateInterface | null) => {
    this.setState({ thisPlacement: value });
  };
  setAction = (value: "RECEIVED" | "NOT_RECEIVED" | null) => {
    this.setState({ action: value });
  };
  setTakeAction = (value: boolean) => {
    this.setState({ takeAction: value });
  };

  submitRequest = async (
    thisPlacement: placedCandidateInterface,
    action: "RECEIVED" | "NOT_RECEIVED"
  ) => {
    if (action === "NOT_RECEIVED" && isEmptyOrSpaces(this.state.comment)) {
      return this.props.setFormError({
        element: "received_comment",
        msg: "Reason of your decision is required!",
      });
    }

    let received: number = 0;
    if (action === "NOT_RECEIVED") {
      received = this.props.thisPost.received_posts
        ? this.props.thisPost.received_posts
        : 0;
    } else {
      received = this.props.thisPost.received_posts
        ? this.props.thisPost.received_posts + 1
        : 1;
    }

    this.props.setLoadingRequest(true);
    try {
      if (this.props.thisPost) {
        const res = await axios.patch<{ data: PostSchoolInterface }>(
          `${API.PLACEMENT}/requestpost/${this.props.thisPost._id}`,
          {
            confirmed_posts: this.props.thisPost.confirmed_posts,
            confirmed_by: this.props.auth.user.user_id,
            received_posts: received,
            approval_date: this.props.thisPost.approval_date,
            school_id: this.props.thisPost.school_id,
            position_id: this.props.thisPost.position_id,
            district_id: this.props.thisPost.district_id,
            needed_posts: this.props.thisPost.needed_posts,
            male_number: this.props.thisPost.male_number,
            female_number: this.props.thisPost.female_number,
            description: this.props.thisPost.description,
            occupied_posts: this.props.thisPost.occupied_posts,
            requested_by: this.props.auth.user.user_id,
            requested_post_status: PostRequestStatusInterface.RECEIVED,
          }
        );
        if (res) {
          // props.updateSchoolPost("SCHOOL", res.data.data);
          this.confirmPlacement(thisPlacement);

          // !this.props.viewPost && this.props.setThisPost(null);
          // this.props.setReceivedPosts(0);
          // this.props.setModalState(null);
          // this.props.PushPopupAlert({
          //   title: `Post request has been received!`,
          //   theme: PopupAlertTheme.info,
          // });
        }
      }
    } catch (error) {
      console.error({ role_error: error });
      this.props.PushPopupAlert({
        title: errorToText(error),
        theme: PopupAlertTheme.danger,
      });
    }
    this.props.setLoadingRequest(false);
  };

  confirmPlacement = async (thisPlacement: placedCandidateInterface) => {
    this.props.setLoadingRequest(true);
    try {
      if (this.props.thisPost) {
        const res = await axios.post<{ data: PostSchoolInterface }>(
          `${API.PLACEMENT}/confirmreceived`,
          {
            placedStatus: [
              {
                waiting_list_id: thisPlacement.waiting_list_id,
                placed_candidate_id: thisPlacement._id,
                status:
                  this.state.action === "NOT_RECEIVED"
                    ? PlacedCandidateStatus.REJECTED
                    : PlacedCandidateStatus.PLACED,
              },
            ],
          }
        );
        if (res) {
          this.props.fetchSchoolPosts(
            this.props.auth.user_access.school_id,
            (status: boolean, message: string) => {
              if (!status) {
                if (message.length > 0 && this.props.loadingPost) {
                  console.error("error loading school post: ", message);
                  this.props.PushPopupAlert({
                    title: message,
                    theme: PopupAlertTheme.danger,
                  });
                }
                this.props.setLoadingPost(false);
              }
            }
          );

          this.props.getSchoolPlacement(
            this.props.auth.user_access.school_id,
            (
              status: boolean,
              msg: string,
              data?: placedCandidateInterface[]
            ) => {
              this.props.setLoadingPost(status);
              if (!status && msg.length > 0) {
                console.error({ getSchoolPlacementError: msg });
                this.props.PushPopupAlert({
                  title: msg,
                  theme: PopupAlertTheme.danger,
                });
              }
            }
          );

          this.props.setFormError(null);
          this.setState({ comment: "", placement_id: "", action: null });
          !this.props.viewPost && this.props.setThisPost(null);
          this.props.setReceivedPosts(0);
          this.props.setModalState(null);
          this.props.PushPopupAlert({
            title: `Request has been done completely!`,
            theme: PopupAlertTheme.info,
          });
        }
      }
    } catch (error) {
      console.error({ role_error: error });
      this.props.PushPopupAlert({
        title: errorToText(error),
        theme: PopupAlertTheme.danger,
      });
    }
    this.props.setLoadingRequest(false);
  };

  render() {
    return (
      <section>
        <div className="border-b-2 border-gray-200 mb-2 pb-1 flex items-center gap-3">
          <button
            className={`ml-3 bg-gray-100 hover:bg-blue-100 rounded-full px-1 py-1 focus:outline-none ${
              this.props.loadingRequest ? "cursor-wait" : "cursor-pointer"
            } animate__animated animate__fadeIn`}
            disabled={this.props.loadingRequest}
            onClick={() => {
              this.props.setThisPost(null);
              this.props.setReceivedPosts(0);
              this.props.setModalState(null);
            }}
          >
            <MdArrowBack className="text-4xl" />
          </button>

          <span className="font-bold text-2xl md:text-3xl text-gray-700">
            Confirm teacher arrival
          </span>
        </div>

        <div className="grid grid-cols-1 md:grid-cols-2 gap-4 divide-x-0 divide-y md:divide-x md:divide-y-0">
          <div className="px-1 mb-4 mt-3">
            <div className="text-base leading-6 space-y-4 text-gray-700 sm:text-lg sm:leading-7">
              <div className="flex flex-row item-center justify-left gap-3 mb-2">
                <span className="text-gray-500 italic">Position: </span>
                <span className="font-bold">
                  {getPositionById(
                    this.props.systemPositions,
                    this.props.thisPost.position_id
                  )
                    ? getPositionById(
                        this.props.systemPositions,
                        this.props.thisPost.position_id
                      )!.position_title
                    : "Unknown position"}
                </span>
              </div>

              <table className="w-full shadow" id="report_content">
                <thead className="bg-blue-50 border-b-2 border-gray-300 text-gray-600 font-bold text-sm">
                  <tr>
                    <td className="px-2 py-2">Requested posts</td>
                    <td className="px-2 py-2">Approved posts</td>
                    <td className="px-2 py-2">Placed posts</td>
                    <td className="px-2 py-2">Received posts</td>
                    <td className="px-2 py-2">Not received posts</td>
                    <td className="px-2 py-2">Status</td>
                  </tr>
                </thead>
                <tbody>
                  <tr>
                    <td className="px-2 py-2">
                      {this.props.thisPost.needed_posts}
                    </td>
                    <td className="px-2 py-2">
                      {this.props.thisPost.requested_post_status !==
                      PostRequestStatusInterface.INITIATED ? (
                        <div className="flex items-center gap-1">
                          {this.props.thisPost.confirmed_posts}
                        </div>
                      ) : (
                        "-"
                      )}
                    </td>
                    <td className="px-2 py-2">
                      {this.props.thisPost.requested_post_status !==
                      PostRequestStatusInterface.INITIATED ? (
                        <span>
                          {
                            getPlacementByRequest(
                              this.props.placementList,
                              this.props.thisPost._id
                            ).length
                          }
                        </span>
                      ) : (
                        "-"
                      )}
                    </td>
                    <td className="px-2 py-2">
                      {this.props.thisPost.requested_post_status ===
                      PostRequestStatusInterface.RECEIVED
                        ? this.props.thisPost.received_posts
                        : "-"}
                    </td>
                    <td className="px-2 py-2">
                      {this.props.thisPost.requested_post_status ===
                      PostRequestStatusInterface.RECEIVED ? (
                        <span>
                          {
                            getPlacementByRequest(
                              this.props.placementList,
                              this.props.thisPost._id,
                              PlacedCandidateStatus.REJECTED
                            ).length
                          }
                        </span>
                      ) : (
                        "0"
                      )}
                    </td>

                    <td className="px-2 py-2">
                      <span
                        className={`px-2 py-1 mx-1 font-semibold rounded ${
                          this.props.thisPost.requested_post_status ===
                          PostRequestStatusInterface.INITIATED
                            ? "text-gray-700 bg-gray-100 dark:bg-gray-700 dark:text-gray-100"
                            : this.props.thisPost.requested_post_status ===
                              PostRequestStatusInterface.CONFIRMED
                            ? "text-blue-700 bg-blue-100 dark:bg-blue-700 dark:text-blue-100"
                            : "text-green-800 bg-green-100 dark:bg-green-800 dark:text-green-100"
                        }`}
                      >
                        {showRequestStatus(
                          this.props.thisPost.requested_post_status,
                          getPlacementByRequest(
                            this.props.placementList,
                            this.props.thisPost._id,
                            "all"
                          ).length,
                          getPlacementByRequest(
                            this.props.placementList,
                            this.props.thisPost._id,
                            PlacedCandidateStatus.PLACED
                          ).length,
                          getPlacementByRequest(
                            this.props.placementList,
                            this.props.thisPost._id,
                            PlacedCandidateStatus.REJECTED
                          ).length
                        )}
                      </span>
                    </td>
                  </tr>
                </tbody>
              </table>

              <div className="my-2">
                {getPositionById(
                  this.props.systemPositions,
                  this.props.thisPost.position_id
                ) && (
                  <ViewPosition
                    thisPosition={
                      getPositionById(
                        this.props.systemPositions,
                        this.props.thisPost.position_id
                      )!
                    }
                    isGuest={true}
                  />
                )}
              </div>
            </div>
          </div>

          <div className="px-4 mt-3 mb-4">
            {getPlacementByRequest(
              this.props.placementList,
              this.props.thisPost._id
            ).map((item, index) => (
              <div
                className="relative grid grid-cols-1 gap-4 px-4 py-1 mb-8 border rounded bg-white shadow"
                key={index + item._id}
              >
                <div className="relative flex gap-4">
                  {/* IMG ================== */}
                  <img
                    src={API.AUTH_FILES + "/" + item.user_info.photo}
                    className="object-center object-cover relative rounded-lg -top-4 -mb-3 bg-white border h-20 w-20"
                    alt=""
                    loading="lazy"
                  />
                  <div className="flex flex-col w-full">
                    <div className="flex flex-row justify-between">
                      <p className="relative text-xl whitespace-nowrap truncate overflow-hidden">
                        {item.user_info.fname + " " + item.user_info.lname}
                      </p>
                    </div>
                    <p className="text-gray-500 text-sm flex items-center gap-1">
                      <MdSmartphone /> {item.phone_number}
                    </p>
                    <p className="text-gray-500 text-sm flex items-center gap-1">
                      <MdMail /> {item.email}
                    </p>
                  </div>
                </div>
                <div className="-mt-3 overflow-x-hidden">
                  {this.state.action !== "NOT_RECEIVED" && (
                    <div className="flex flex-row-reverse gap-2">
                      <div className="flex-initial">
                        <button
                          type="button"
                          className="btn btn-blue flex items-center font-medium tracking-wide capitalize transition duration-300 transform active:scale-95 ease-in-out"
                          disabled={this.props.loadingRequest}
                          onClick={() => {
                            this.setState({
                              placement_id: item._id,
                              action: "RECEIVED",
                              thisPlacement: item,
                              takeAction: true,
                            });
                            // this.submitRequest(item, "RECEIVED");
                          }}
                        >
                          <MdTaskAlt />
                          <span className="pl-2 mx-1">
                            {this.props.loadingRequest &&
                            item._id === this.state.placement_id
                              ? "Loading..."
                              : "Received"}
                          </span>
                        </button>
                      </div>
                      <div className="flex-initial">
                        <button
                          type="button"
                          className="btn flex items-center px-5 py-2.5 font-medium tracking-wide text-black capitalize hover:bg-red-200 hover:fill-current hover:text-red-600 focus:outline-none transition duration-300 transform active:scale-95 ease-in-out"
                          disabled={this.props.loadingRequest}
                          onClick={() =>
                            this.setState({
                              placement_id: item._id,
                              action: "NOT_RECEIVED",
                              thisPlacement: item,
                            })
                          }
                        >
                          <MdErrorOutline />
                          <span className="pl-2 mx-1">Not received</span>
                        </button>
                      </div>
                    </div>
                  )}
                  {this.state.action === "NOT_RECEIVED" &&
                    this.state.placement_id === item._id && (
                      <div className="flex flex-col md:flex-row items-end md:items-start gap-2 animate__animated animate__fadeInRight animate__faster mt-2">
                        <div className="w-full">
                          <textarea
                            className={`border-2 rounded p-2 md:text-base w-full ${
                              this.props.formError &&
                              this.props.formError.element ===
                                "received_comment"
                                ? "border-red-500"
                                : "border-gray-300"
                            } focus:border-gray-500`}
                            placeholder="comment"
                            onChange={(e) => {
                              this.setState({ comment: e.target.value });
                              this.props.formError &&
                                this.props.formError.element ===
                                  "received_comment" &&
                                this.props.setFormError(null);
                            }}
                            value={this.state.comment}
                            style={{ resize: "none" }}
                          ></textarea>
                          {this.props.formError &&
                            this.props.formError.element ===
                              "received_comment" && (
                              <span className="text-red-700 text-sm">
                                {this.props.formError.msg}
                              </span>
                            )}
                        </div>

                        <div>
                          <button
                            type="button"
                            className="btn bg-gray-100 hover:bg-gray-400 w-full font-medium tracking-wide capitalize transition duration-300 transform active:scale-95 ease-in-out"
                            disabled={this.props.loadingRequest}
                            onClick={() => this.setTakeAction(true)}
                          >
                            {this.props.loadingRequest &&
                            item._id === this.state.placement_id
                              ? "Loading..."
                              : "Confirm"}
                          </button>
                          <button
                            type="button"
                            className="btn bg-red-100  w-full hover:bg-red-400 font-medium tracking-wide capitalize transition duration-300 transform active:scale-95 ease-in-out"
                            disabled={this.props.loadingRequest}
                            onClick={() =>
                              this.setState({
                                placement_id: "",
                                action: null,
                                thisPlacement: null,
                              })
                            }
                          >
                            {this.props.loadingRequest &&
                            item._id === this.state.placement_id
                              ? "Loading..."
                              : "Cancel"}
                          </button>
                        </div>
                      </div>
                    )}
                </div>
              </div>
            ))}
          </div>
        </div>

        {this.state.thisPlacement &&
          this.state.action &&
          this.state.takeAction && (
            <ActionConfirm
              confirmFun={() => {
                this.setTakeAction(false);
                this.submitRequest(
                  this.state.thisPlacement!,
                  this.state.action!
                );
              }}
              setModalAction={(value: boolean) => {
                this.setTakeAction(value);
                this.setThisPlacement(null);
                this.setAction(null);
                this.setState({
                  placement_id: "",
                });
              }}
              description={
                this.state.action === "NOT_RECEIVED"
                  ? `Do you really confirm that ${this.state.thisPlacement.user_info.fname} ${this.state.thisPlacement.user_info.lname} didn't or will not come?`
                  : `Do you really confirm that you have received ${this.state.thisPlacement.user_info.fname} ${this.state.thisPlacement.user_info.lname}?`
              }
              modalType={ActionConfirmEnum.INFO}
            />
          )}
      </section>
    );
  }
}

const mapStateToProps = ({
  auth,
}: StoreState): {
  auth: Auth;
} => {
  return {
    auth: auth,
  };
};
export default connect(mapStateToProps, {
  fetchSchoolPosts,
  getSchoolPlacement,
  PushPopupAlert,
})(ReceivePost);
