import React, { Component } from "react";
import { GiTeacher } from "react-icons/gi";
import { MdInfoOutline } from "react-icons/md";
import { connect } from "react-redux";
import { Redirect } from "react-router-dom";
import {
  Auth,
  loadSchoolLevels,
  PushPopupAlert,
  schoolLevelsStoreInterface,
} from "../../actions";
import {
  getSchoolById,
  fetchSchoolPosts,
  createSchoolPost,
  updateSchoolPost,
  getSchoolPlacement,
} from "../../actions/schools";
import { getActiveAcademicSession } from "../../actions/session";
import { FC_GetPositionsByInstitution } from "../../actions/settings";
import { PopupAlertData } from "../../components/Alert/Popup";
import { AcademicSessionInterface, PositionAPI } from "../../interfaces";
import {
  placedCandidateInterface,
  PlacedCandidateStatus,
} from "../../interfaces/marks_publication";
import { PopupAlertTheme } from "../../interfaces/popupAlert";
import { StoreState } from "../../reducers";
import { isEmptyOrSpaces } from "../../shared/dataTest";
import {
  PostSchoolInterface,
  MySchoolInterface,
} from "../../shared/interfaces";
import ReceivePost from "./ReceivePost";
import RequestList from "./RequestList";
import RequestPostForm from "./RequestPostForm";

export const getPlacementByRequest = (
  placements: placedCandidateInterface[],
  request: string,
  status: PlacedCandidateStatus | "all" = PlacedCandidateStatus.PENDING
): placedCandidateInterface[] => {
  console.log("====================================");
  console.log({ placements, request });
  console.log("====================================");
  if (status !== "all") {
    // position id must be replaced with request id
    return placements.filter(
      (item) => item.request_id === request && item.status === status
    );
  }
  return placements.filter((item) => item.request_id === request);
};

interface RequestTeacherProps {
  auth: Auth;
  mySchool: MySchoolInterface | null;
  schoolLevels: schoolLevelsStoreInterface;
  systemPositions: PositionAPI[] | null;
  schoolPosts: PostSchoolInterface[];
  schoolPlacements: placedCandidateInterface[] | null;
  activeAcademicSession: AcademicSessionInterface | "none" | null;
  getActiveAcademicSession: Function;
  FC_GetPositionsByInstitution: Function;
  fetchSchoolPosts: Function;
  getSchoolPlacement: Function;
  createSchoolPost: Function;
  updateSchoolPost: Function;
  getSchoolById: Function;
  loadSchoolLevels: (callback: (loading: boolean) => void) => void;
  PushPopupAlert: (alert: PopupAlertData) => void;
}

interface RequestTeacherState {
  loading: boolean;
  loadingPost: boolean;
  loadingRequest: boolean;
  modalState: "CREATE" | "UPDATE" | "RECEIVE" | null;
  filterByPosition: string;
  positionId: string;
  requestedPosts: number;
  femaleOnPosition: number;
  maleOnPosition: number;
  requestExplanation: string;
  formSteps: {
    ONE: boolean;
    TWO: boolean;
  };
  stepState: {
    ONE: boolean;
    TWO: boolean;
  };
  thisPost: PostSchoolInterface | null;
  viewPost: boolean;
  postId: string;
  receivedPosts: number;
  formError: {
    element: string;
    msg: string;
  } | null;
}
class RequestTeacher extends Component<
  RequestTeacherProps,
  RequestTeacherState
> {
  constructor(props: RequestTeacherProps) {
    super(props);

    this.state = {
      loading: true,
      loadingPost: true,
      loadingRequest: false,
      modalState: null,
      filterByPosition: "",
      positionId: "",
      requestedPosts: 0,
      femaleOnPosition: 0,
      maleOnPosition: 0,
      requestExplanation: "",
      formSteps: { ONE: true, TWO: false },
      stepState: { ONE: false, TWO: false },
      thisPost: null,
      viewPost: false,
      postId: "",
      receivedPosts: 0,
      formError: null,
    };
  }

  setLoading = (value: boolean) => {
    this.setState({ loading: value });
  };

  setLoadingRequest = (value: boolean) => {
    this.setState({ loadingRequest: value });
  };

  setLoadingPost = (value: boolean) => {
    this.setState({ loadingPost: value });
  };

  setViewPost = (value: boolean) => {
    this.setState({ viewPost: value });
  };

  setPostId = (value: string) => {
    this.setState({ postId: value });
  };

  setFormError = (
    value: {
      element: string;
      msg: string;
    } | null
  ) => {
    this.setState({ formError: value });
  };

  setPositionId = (value: string) => {
    this.setState({ positionId: value });
  };
  setRequestedPosts = (value: number) => {
    this.setState({ requestedPosts: value });
  };
  setFemaleOnPosition = (value: number) => {
    this.setState({ femaleOnPosition: value });
  };
  setMaleOnPosition = (value: number) => {
    this.setState({ maleOnPosition: value });
  };
  setRequestExplanation = (value: string) => {
    this.setState({ requestExplanation: value });
  };
  setFormSteps = (value: { ONE: boolean; TWO: boolean }) => {
    this.setState({ formSteps: value });
  };
  setStepState = (value: { ONE: boolean; TWO: boolean }) => {
    this.setState({ stepState: value });
  };
  setFilterByPosition = (value: string) => {
    this.setState({ filterByPosition: value });
  };

  setThisPost = (value: PostSchoolInterface | null) => {
    this.setState({ thisPost: value });
  };

  setModalState = (value: "CREATE" | "UPDATE" | "RECEIVE" | null) => {
    this.setState({ modalState: value });
  };

  setReceivedPosts = (value: number) => {
    this.setState({ receivedPosts: value });
  };

  loadData = () => {
    if (!this.props.mySchool) {
      this.props.getSchoolById(
        this.props.auth.user_access.school_id,
        (status: boolean, msg: string) => {
          this.setLoading(status);
          if (msg.length > 0) {
            console.error({ loadingCandidatesError: msg });
            this.props.PushPopupAlert({
              title: msg,
              theme: PopupAlertTheme.danger,
            });
            this.setLoading(false);
          }
        }
      );
    } else {
      this.setLoading(false);
    }

    if (!this.props.schoolPlacements) {
      this.props.getSchoolPlacement(
        this.props.auth.user_access.school_id,
        (status: boolean, msg: string, data?: placedCandidateInterface[]) => {
          this.setLoading(status);
          if (!status && msg.length > 0) {
            console.error({ getSchoolPlacementError: msg });
            this.props.PushPopupAlert({
              title: msg,
              theme: PopupAlertTheme.danger,
            });
          }
        }
      );
    } else {
      this.setLoading(false);
    }

    if (
      (this.props.systemPositions === null ||
        (this.props.systemPositions.length === 0 && this.state.loading)) &&
      this.props.auth.education_institution_id
    ) {
      this.setLoading(true);
      this.props.FC_GetPositionsByInstitution(
        this.props.auth.education_institution_id,
        (status: boolean, msg: string) => {
          this.setLoading(status);
          console.log({ status: status, msg: msg });
          if (msg.length) {
            this.props.PushPopupAlert({
              title: msg,
              theme: PopupAlertTheme.danger,
            });
          }
        }
      );
    } else {
      this.setLoading(false);
    }

    if (
      this.props.schoolLevels.school_levels === null ||
      (this.props.schoolLevels.school_levels.length === 0 && this.state.loading)
    ) {
      this.props.loadSchoolLevels(this.setLoading);
    }

    if (!this.props.schoolPosts.length && this.state.loadingPost) {
      this.props.fetchSchoolPosts(
        this.props.auth.user_access.school_id,
        (status: boolean, message: string) => {
          if (!status) {
            if (message.length > 0 && this.state.loadingPost) {
              console.error("error loading school post: ", message);
              this.props.PushPopupAlert({
                title: message,
                theme: PopupAlertTheme.danger,
              });
            }
            this.setState({ loadingPost: false });
          }
        }
      );
    } else {
      this.setState({ loadingPost: false });
    }
  };

  componentDidMount = () => {
    if (isEmptyOrSpaces(this.props.auth.user_access.school_id)) {
      return <Redirect to="/apps-page" />;
    }
    if (
      this.props.activeAcademicSession === null ||
      this.props.activeAcademicSession === "none"
    ) {
      this.props.getActiveAcademicSession(
        (
          status: boolean,
          msg: string,
          data?: AcademicSessionInterface | "none"
        ) => {
          this.setLoading(status);
          if (msg.length > 0) {
            console.error({ loadingCandidatesError: msg });
            this.props.PushPopupAlert({
              title: msg,
              theme: PopupAlertTheme.danger,
            });
          }
          if (data && data !== "none") this.loadData();
        }
      );
    } else this.loadData();
  };

  getPositionById = (
    systemPositions: PositionAPI[],
    positionId: string
  ): PositionAPI | undefined => {
    return systemPositions.find((item) => item.position_id === positionId);
  };

  render() {
    if (
      this.props.activeAcademicSession !== null &&
      this.props.activeAcademicSession === "none"
    ) {
      return (
        <div className="bg-yellow-100 rounded flex flex-col items-center justify-center p-6 my-8 text-yellow-900 border border-yellow-900">
          <MdInfoOutline className="text-6xl md:text-9xl" />
          <div className="text-center flex flex-col">
            <span className="text-2xl md:text-4xl font-extrabold">
              No academic active session
            </span>
            <span className="text-lg">
              Please Wait for the system to publish the sessions or contact the
              administrator
            </span>
          </div>
        </div>
      );
    }
    if (this.state.loading) {
      return (
        <div className="text-center mt-7 font-bold text-4xl text-gray-400 animate-pulse">
          Loading data...
        </div>
      );
    }

    return (
      <div>
        {/* header title */}
        <div className="flex flex-row items-start gap-3 my-2">
          <GiTeacher className="text-blue-700 w-10 h-10 md:block hidden" />
          <div className="w-full">
            <div className="flex justify-between w-full">
              <div className="flex flex-col gap-1 justify-between">
                <h1 className="md:text-4xl text-3xl font-semibold">
                  Teacher Request
                </h1>

                <div className="flex-row items-center md:gap-3 gap-2">
                  <div className="flex flex-row items-center gap-1">
                    <span className="text-gray-600 text-lg">
                      Academic session:
                    </span>
                    <span className="text-gray-600 font-semibold text-xl">
                      {this.props.activeAcademicSession?.title}
                    </span>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        {/* end header title */}

        <div className="bg-white shadow p-2 rounded">
          {!this.state.modalState && this.props.schoolPlacements && (
            <RequestList
              systemPositions={this.props.systemPositions}
              setModalState={this.setModalState}
              loadingRequest={this.state.loadingRequest}
              setFilterByPosition={this.setFilterByPosition}
              schoolPosts={this.props.schoolPosts}
              setThisPost={this.setThisPost}
              setViewPost={this.setViewPost}
              setPostId={this.setPostId}
              setPositionId={this.setPositionId}
              setRequestedPosts={this.setRequestedPosts}
              setRequestExplanation={this.setRequestExplanation}
              setFemaleOnPosition={this.setFemaleOnPosition}
              setMaleOnPosition={this.setMaleOnPosition}
              setFormSteps={this.setFormSteps}
              setStepState={this.setStepState}
              thisPost={this.state.thisPost}
              viewPost={this.state.viewPost}
              modalState={this.state.modalState}
              schoolPlacements={this.props.schoolPlacements}
              filterByPosition={this.state.filterByPosition}
            />
          )}

          {this.props.activeAcademicSession &&
            (this.state.modalState === "CREATE" ||
              this.state.modalState === "UPDATE") && (
              <RequestPostForm
                loadingRequest={this.state.loadingRequest}
                setLoadingRequest={this.setLoadingRequest}
                formSteps={this.state.formSteps}
                setFormSteps={this.setFormSteps}
                stepState={this.state.stepState}
                setStepState={this.setStepState}
                sessionId={this.props.activeAcademicSession?._id}
                postId={this.state.postId}
                setPostId={this.setPostId}
                positionId={this.state.positionId}
                setPositionId={this.setPositionId}
                setRequestedPosts={this.setRequestedPosts}
                requestedPosts={this.state.requestedPosts}
                maleOnPosition={this.state.maleOnPosition}
                femaleOnPosition={this.state.femaleOnPosition}
                setFemaleOnPosition={this.setFemaleOnPosition}
                setMaleOnPosition={this.setMaleOnPosition}
                requestExplanation={this.state.requestExplanation}
                setRequestExplanation={this.setRequestExplanation}
                modalState={this.state.modalState}
                setModalState={this.setModalState}
                formError={this.state.formError}
                setFormError={this.setFormError}
              />
            )}

          {this.state.modalState === "RECEIVE" &&
            this.state.thisPost &&
            this.props.systemPositions &&
            this.props.schoolPlacements && (
              <ReceivePost
                loadingPost={this.state.loadingPost}
                loadingRequest={this.state.loadingRequest}
                positionId={this.state.positionId}
                thisPost={this.state.thisPost}
                viewPost={this.state.viewPost}
                receivedPosts={this.state.receivedPosts}
                formError={this.state.formError}
                systemPositions={this.props.systemPositions}
                setThisPost={this.setThisPost}
                setModalState={this.setModalState}
                setFormError={this.setFormError}
                setReceivedPosts={this.setReceivedPosts}
                setLoadingRequest={this.setLoadingRequest}
                setLoadingPost={this.setLoadingPost}
                placementList={this.props.schoolPlacements}
              />
            )}
        </div>
      </div>
    );
  }
}

const mapStateToProps = ({
  auth,
  schools,
  system_settings,
  schoolLevels,
  sessionPlan,
}: StoreState): {
  auth: Auth;
  mySchool: MySchoolInterface | null;
  schoolPosts: PostSchoolInterface[];
  schoolPlacements: placedCandidateInterface[] | null;
  schoolLevels: schoolLevelsStoreInterface;
  systemPositions: PositionAPI[] | null;
  activeAcademicSession: AcademicSessionInterface | "none" | null;
} => {
  return {
    auth: auth,
    mySchool: schools.mySchool,
    schoolPosts: schools.schoolPosts,
    schoolPlacements: schools.schoolPlacements,
    schoolLevels,
    systemPositions: system_settings.systemPositions,
    activeAcademicSession: sessionPlan.activeAcademicSession,
  };
};

export const PostRequest = connect(mapStateToProps, {
  loadSchoolLevels,
  FC_GetPositionsByInstitution,
  getSchoolById,
  fetchSchoolPosts,
  getSchoolPlacement,
  createSchoolPost,
  updateSchoolPost,
  getActiveAcademicSession,
  PushPopupAlert,
})(RequestTeacher);
