import axios from "axios";
import React, { Component } from "react";
import { connect } from "react-redux";
import {
  Auth,
  PushPopupAlert,
  schoolLevelsStoreInterface,
} from "../../actions";
import { createSchoolPost, updateSchoolPost } from "../../actions/schools";
import { PopupAlertData } from "../../components/Alert/Popup";
import { PositionAPI } from "../../interfaces";
import { PopupAlertTheme } from "../../interfaces/popupAlert";
import { StoreState } from "../../reducers";
import { getPositionById, isEmptyOrSpaces } from "../../shared/dataTest";
import {
  MySchoolInterface,
  PostRequestStatusInterface,
  PostSchoolInterface,
} from "../../shared/interfaces";
import { API } from "../../utils/api";
import { errorToText } from "../../utils/errors";
import ViewPosition from "../Positions/ViewPosition";
import StepsHeader from "./StepsHeader";

interface RequestPostFormProps {
  loadingRequest: boolean;
  setLoadingRequest: (value: boolean) => void;
  formSteps: {
    ONE: boolean;
    TWO: boolean;
  };
  setFormSteps: (value: { ONE: boolean; TWO: boolean }) => void;
  stepState: {
    ONE: boolean;
    TWO: boolean;
  };
  setStepState: (value: { ONE: boolean; TWO: boolean }) => void;
  sessionId: string;
  postId: string;
  setPostId: (value: string) => void;
  positionId: string;
  setPositionId: (value: string) => void;
  setRequestedPosts: (value: number) => void;
  requestedPosts: number;
  maleOnPosition: number;
  femaleOnPosition: number;
  setFemaleOnPosition: (value: number) => void;
  setMaleOnPosition: (value: number) => void;
  requestExplanation: string;
  setRequestExplanation: (value: string) => void;
  modalState: "CREATE" | "UPDATE" | "RECEIVE" | null;
  setModalState: (value: "CREATE" | "UPDATE" | "RECEIVE" | null) => void;
  formError: {
    element: string;
    msg: string;
  } | null;
  setFormError: (
    value: {
      element: string;
      msg: string;
    } | null
  ) => void;

  //   REDUX =================
  auth: Auth;
  mySchool: MySchoolInterface | null;
  schoolPosts: PostSchoolInterface[];
  schoolLevels: schoolLevelsStoreInterface;
  systemPositions: PositionAPI[] | null;
  createSchoolPost: Function;
  updateSchoolPost: Function;
  PushPopupAlert: (alert: PopupAlertData) => void;
}

interface RequestPostFormState {
  selectedLevel: string;
  error: string | null;
}

export class RequestPostForm extends Component<
  RequestPostFormProps,
  RequestPostFormState
> {
  constructor(props: RequestPostFormProps) {
    super(props);

    this.state = {
      selectedLevel: "",
      error: null,
    };
  }

  GetAllowedPositions = (position_id?: string): PositionAPI[] => {
    if (this.props.systemPositions) {
      if (this.props.schoolPosts.length > 0) {
        let postPositionIds: string[] = [];
        for (const key in this.props.schoolPosts) {
          if (
            Object.prototype.hasOwnProperty.call(this.props.schoolPosts, key)
          ) {
            if (
              this.props.schoolPosts[key].requested_post_status !==
                PostRequestStatusInterface.RECEIVED ||
              (this.props.schoolPosts[key].requested_post_status !==
                PostRequestStatusInterface.CONFIRMED &&
                this.props.schoolPosts[key].confirmed_posts &&
                this.props.schoolPosts[key].confirmed_posts === 0)
            ) {
              if (
                !position_id ||
                position_id !== this.props.schoolPosts[key].position_id
              ) {
                postPositionIds.push(this.props.schoolPosts[key].position_id);
              }
            }
          }
        }
        if (position_id) {
          postPositionIds = postPositionIds.filter(
            (item) => item !== position_id
          );
        }
        return this.props.systemPositions.filter(
          (item) => !postPositionIds.includes(item.position_id)
        );
      }
      return this.props.systemPositions;
    }
    return [];
  };

  submitRequest = async (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    // if (this.props.modalState === "RECEIVE" && this.props.receivedPosts < 0) {
    //   return this.props.setFormError({
    //     element: "received_posts",
    //     msg: "Invalid number of received post",
    //   });
    // }
    // if (
    //   this.props.modalState === "RECEIVE" &&
    //   this.props.thisPost &&
    //   this.props.receivedPosts > this.props.thisPost.needed_posts
    // ) {
    //   return this.props.setFormError({
    //     element: "received_posts",
    //     msg: "Invalid number of received post",
    //   });
    // }

    if (this.props.requestedPosts <= 0) {
      return this.props.setFormError({
        element: "requested_posts",
        msg: "Needed post must be greater than 0",
      });
    }

    if (isEmptyOrSpaces(this.props.requestExplanation)) {
      return this.props.setFormError({
        element: "comment",
        msg: "Explanation is required!",
      });
    }

    const thisSchool = this.props.mySchool;
    if (!thisSchool) {
      return this.props.PushPopupAlert({
        title: "No district id found",
        theme: PopupAlertTheme.danger,
      });
    }

    if (
      this.props.modalState === "UPDATE" &&
      isEmptyOrSpaces(this.props.postId)
    ) {
      return this.setState({ error: "Post id not find" });
    }

    this.props.setLoadingRequest(true);
    try {
      if (this.props.modalState === "CREATE") {
        const res = await axios.post<{ data: PostSchoolInterface }>(
          `${API.PLACEMENT}/requestpost`,
          {
            school_id: this.props.auth.user_access.school_id,
            position_id: this.props.positionId,
            district_id: thisSchool.district_id,
            needed_posts: this.props.requestedPosts,
            male_number: this.props.maleOnPosition,
            female_number: this.props.femaleOnPosition,
            // description: 44,
            description: this.props.requestExplanation,
            occupied_posts:
              Number(this.props.maleOnPosition) +
              Number(this.props.femaleOnPosition),
            requested_by: this.props.auth.user.user_id,
            post_request_session_id: this.props.sessionId,
          }
        );
        if (res) {
          this.props.createSchoolPost(res.data.data);
          this.props.setPositionId("");
          this.props.setRequestedPosts(0);
          this.props.setFemaleOnPosition(0);
          this.props.setMaleOnPosition(0);
          this.props.setRequestExplanation("");
          this.props.setFormSteps({ ONE: true, TWO: false });
          this.props.setStepState({ ONE: false, TWO: false });
          this.props.PushPopupAlert({
            title: `Post(s) has been requested!`,
            theme: PopupAlertTheme.success,
          });
        }
      }

      if (this.props.modalState === "UPDATE") {
        console.log("====================================");
        console.log({
          confirmed_posts: 0,
          confirmed_by: null,
          received_posts: 0,
          approval_date: null,
          school_id: this.props.auth.user_access.school_id,
          position_id: this.props.positionId,
          district_id: thisSchool.district_id._id,
          needed_posts: this.props.requestedPosts,
          male_number: this.props.maleOnPosition,
          female_number: this.props.femaleOnPosition,
          description: this.props.requestExplanation,
          occupied_posts:
            Number(this.props.maleOnPosition) +
            Number(this.props.femaleOnPosition),
          requested_by: this.props.auth.user.user_id,
          requested_post_status: PostRequestStatusInterface.INITIATED,
        });
        console.log("====================================");
        const res = await axios.patch<{ data: PostSchoolInterface }>(
          `${API.PLACEMENT}/requestpost/${this.props.postId}`,
          {
            confirmed_posts: 0,
            confirmed_by: null,
            received_posts: 0,
            approval_date: null,
            school_id: this.props.auth.user_access.school_id,
            position_id: this.props.positionId,
            district_id: thisSchool.district_id,
            needed_posts: this.props.requestedPosts,
            male_number: this.props.maleOnPosition,
            female_number: this.props.femaleOnPosition,
            description: this.props.requestExplanation,
            occupied_posts:
              Number(this.props.maleOnPosition) +
              Number(this.props.femaleOnPosition),
            requested_by: this.props.auth.user.user_id,
            requested_post_status: PostRequestStatusInterface.INITIATED,
            post_request_session_id: this.props.sessionId,
          }
        );

        if (res) {
          console.log("====================================");
          console.log({ updateSchoolPost: res.data.data });
          console.log("====================================");
          this.props.updateSchoolPost("SCHOOL", res.data.data);
          this.props.setPostId("");
          this.props.setPositionId("");
          this.props.setRequestedPosts(0);
          this.props.setFemaleOnPosition(0);
          this.props.setMaleOnPosition(0);
          this.props.setRequestExplanation("");
          this.props.setFormSteps({ ONE: true, TWO: false });
          this.props.setStepState({ ONE: false, TWO: false });
          this.props.PushPopupAlert({
            title: `Post(s) has been updated!`,
            theme: PopupAlertTheme.info,
          });
        }
      }
    } catch (error) {
      console.error({ role_error: error });
      this.props.PushPopupAlert({
        title: errorToText(error),
        theme: PopupAlertTheme.danger,
      });
    }
    this.props.setLoadingRequest(false);
  };

  setSchoolLevel = (value: string) => {
    this.setState({ selectedLevel: value });
  };

  getPositionByLevelId = (
    allPosition: PositionAPI[],
    levelId: string
  ): PositionAPI[] => {
    if (isEmptyOrSpaces(levelId)) {
      return allPosition;
    }
    return allPosition.filter(
      (item) => item.school_level_id.school_level_id === levelId
    );
  };

  render() {
    return (
      <div>
        <StepsHeader
          currentStep={this.props.formSteps.TWO ? 2 : 1}
          stepState={this.props.stepState}
          setFormSteps={this.props.setFormSteps}
        />
        <div className="px-4 mx-auto mb-4 mt-3">
          {this.props.formSteps.ONE && (
            <section>
              <div className="flex flex-col mb-4">
                <label>School level</label>
                <select
                  className="border-2 border-gray-700 bg-white p-2 rounded focus:outline-none focus:bg-gray-100"
                  value={this.state.selectedLevel}
                  onChange={(e) => {
                    this.setSchoolLevel(e.target.value);
                  }}
                >
                  <option value="">Choose level</option>
                  {this.props.schoolLevels.school_levels &&
                  this.props.schoolLevels.school_levels.length > 0 ? (
                    this.props.schoolLevels.school_levels.map((item, index) => (
                      <option key={index + 11} value={item.school_level_id}>
                        {item.school_level_name}
                      </option>
                    ))
                  ) : (
                    <option>No school level found</option>
                  )}
                </select>
              </div>

              <div className="flex flex-col">
                <label>Select Position</label>
                <select
                  className="border-2 border-gray-700 bg-white p-2 rounded focus:outline-none focus:bg-gray-100"
                  value={this.props.positionId}
                  onChange={(e) => {
                    this.props.setPositionId(e.target.value);
                  }}
                >
                  <option value="">Choose Position</option>
                  {this.props.systemPositions &&
                  this.getPositionByLevelId(
                    this.GetAllowedPositions(
                      this.props.positionId.length > 0
                        ? this.props.positionId
                        : ""
                    ),
                    this.state.selectedLevel
                  ).length > 0 ? (
                    this.getPositionByLevelId(
                      this.GetAllowedPositions(
                        this.props.positionId.length > 0
                          ? this.props.positionId
                          : ""
                      ),
                      this.state.selectedLevel
                    ).map((position, index) => (
                      <option key={index + 11} value={position.position_id}>
                        {position.position_title}
                      </option>
                    ))
                  ) : (
                    <option>No position found</option>
                  )}
                </select>
              </div>
              {this.props.positionId.length > 0 &&
                this.props.systemPositions &&
                getPositionById(
                  this.props.systemPositions,
                  this.props.positionId
                ) && (
                  <div className="my-2 rounded border">
                    <ViewPosition
                      thisPosition={
                        getPositionById(
                          this.props.systemPositions,
                          this.props.positionId
                        )!
                      }
                      isGuest={true}
                    />
                  </div>
                )}
              <div className="bg-white w-full flex justify-end">
                {this.props.positionId.length > 0 && (
                  <button
                    className="btn btn-blue m-1 mr-2 mb-2 animate__animated animate__fadeIn focus:outline-none"
                    onClick={() => {
                      this.props.setFormSteps({
                        ONE: false,
                        TWO: true,
                      });
                      this.props.setStepState({
                        ONE: true,
                        TWO: false,
                      });
                    }}
                  >
                    Continue
                  </button>
                )}
              </div>
            </section>
          )}
          <form onSubmit={this.submitRequest}>
            <div className="bg-white">
              {this.props.formSteps.TWO && (
                <div className="text-base leading-6 space-y-4 text-gray-700 sm:text-lg sm:leading-7">
                  {this.state.error && (
                    <div
                      className="alert-warning animate__animated animate__fadeInRight animate__faster"
                      onClick={() => this.setState({ error: null })}
                    >
                      {this.state.error}
                    </div>
                  )}
                  <div className="mb-4 md:flex md:justify-between">
                    <div className="mb-4 md:mr-2 md:mb-0 w-full">
                      <label
                        className="block mb-2 text-sm font-bold text-gray-700"
                        htmlFor="female"
                      >
                        Current number of Female on this position
                      </label>
                      <input
                        className="appearance-none rounded w-full py-1 px-4 border-2 border-gray-700 bg-white focus:outline-none focus:bg-gray-100"
                        id="female"
                        type="number"
                        min={0}
                        value={this.props.femaleOnPosition}
                        onChange={(e) =>
                          this.props.setFemaleOnPosition(Number(e.target.value))
                        }
                        placeholder="Current number of Female on this position"
                      />
                      {/* <p className="text-xs italic text-red-500">
                          Please choose a password.
                        </p> */}
                    </div>
                    <div className="md:ml-2 w-full">
                      <label
                        className="block mb-2 text-sm font-bold text-gray-700"
                        htmlFor="male"
                      >
                        Current number of Male on this position
                      </label>
                      <input
                        className="appearance-none rounded w-full py-1 px-4 border-2 border-gray-700 bg-white focus:outline-none focus:bg-gray-100"
                        id="male"
                        type="number"
                        min={0}
                        value={this.props.maleOnPosition}
                        onChange={(e) =>
                          this.props.setMaleOnPosition(Number(e.target.value))
                        }
                        placeholder="Current number of Male on this post"
                      />
                    </div>
                  </div>
                  <div className="flex flex-col">
                    <label>Needed posts (number)</label>
                    <input
                      type="number"
                      className={`appearance-none rounded w-full py-1 px-4 border-2 bg-white focus:outline-none focus:bg-gray-100 ${
                        this.props.formError &&
                        this.props.formError.element === "requested_posts"
                          ? "border-red-700"
                          : "border-gray-700"
                      }`}
                      value={this.props.requestedPosts}
                      onChange={(e) => {
                        this.props.setRequestedPosts(Number(e.target.value));
                        this.props.formError &&
                          this.props.formError.element === "requested_posts" &&
                          this.props.setFormError(null);
                      }}
                      placeholder="1"
                    />
                    {this.props.formError &&
                      this.props.formError.element === "requested_posts" && (
                        <span className="text-xs italic text-red-700 mt-1">
                          {this.props.formError.msg}
                        </span>
                      )}
                  </div>
                  <div className="flex flex-col">
                    <label>Explanation</label>
                    <textarea
                      name="description"
                      className={`appearance-none rounded w-full py-1 px-4 border-2 bg-white focus:outline-none focus:bg-gray-100 ${
                        this.props.formError &&
                        this.props.formError.element === "comment"
                          ? "border-red-700"
                          : "border-gray-700"
                      }`}
                      id="description"
                      placeholder="Explanation"
                      value={this.props.requestExplanation}
                      onChange={(e) => {
                        this.props.setRequestExplanation(e.target.value);
                        this.props.formError &&
                          this.props.formError.element === "comment" &&
                          this.props.setFormError(null);
                      }}
                      rows={2}
                    ></textarea>
                    {this.props.formError &&
                      this.props.formError.element === "comment" && (
                        <span className="text-xs italic text-red-700 mt-1">
                          {this.props.formError.msg}
                        </span>
                      )}
                  </div>
                </div>
              )}
              <div className="pt-4 flex justify-between">
                <button
                  className="btn bg-gray-400 text-white hover:bg-gray-600"
                  onClick={() => {
                    this.props.setPositionId("");
                    this.props.setRequestedPosts(0);
                    this.props.setFemaleOnPosition(0);
                    this.props.setMaleOnPosition(0);
                    this.props.setRequestExplanation("");
                    this.props.setModalState(null);
                    this.props.setFormError(null);
                    this.props.setFormSteps({ ONE: true, TWO: false });
                    this.props.setStepState({ ONE: false, TWO: false });
                  }}
                  disabled={this.props.loadingRequest}
                >
                  Exit
                </button>
                {this.props.formSteps.TWO && (
                  <button
                    className="btn bg-primary text-white hover:bg-blue-600"
                    disabled={this.props.loadingRequest}
                  >
                    {this.props.loadingRequest
                      ? "Loading..."
                      : this.props.modalState === "UPDATE"
                      ? "Update"
                      : "Request"}
                  </button>
                )}
              </div>
            </div>
          </form>
        </div>
      </div>
    );
  }
}

const mapStateToProps = ({
  auth,
  schools,
  system_settings,
  schoolLevels,
}: StoreState): {
  auth: Auth;
  mySchool: MySchoolInterface | null;
  schoolPosts: PostSchoolInterface[];
  schoolLevels: schoolLevelsStoreInterface;
  systemPositions: PositionAPI[] | null;
} => {
  return {
    auth: auth,
    mySchool: schools.mySchool,
    schoolPosts: schools.schoolPosts,
    schoolLevels,
    systemPositions: system_settings.systemPositions,
  };
};

export default connect(mapStateToProps, {
  createSchoolPost,
  updateSchoolPost,
  PushPopupAlert,
})(RequestPostForm);
